export default class BaseField {
    /**
     * Sets input config.
     *
     * @param {Object} param
     * @param {string} param.field
     * @param {string} param.label
     * @param {string} param.example
     * @param {string} param.type
     * @param {boolean} param.required
     * @returns {Object}
     */
    input = ({field, label = null, example = null, type = 'text', required = true}) => {
        const inputField = {
            field: field,
            label: `field:${label ?? field}`,
            example: `${example ?? field}Example`,
            inputType: type
        };

        if (required) {
            inputField.required = required;
        }
        return inputField;
    };

    /**
     * Sets author's textarea config.
     *
     * @param {Object} param
     * @param {string} param.field
     * @param {string} param.label
     * @param {string} param.example
     * @param {boolean} param.required
     * @returns {Object}
     */
    author = ({field, label = null, example = null, required = true}) => {
        const authorTextarea = {
            field: field,
            label: `field:${label ?? field}`,
            example: `${example ?? field}Example`,
            author: true,
        };

        if (required) {
            authorTextarea.required = required;
        }
        return authorTextarea;
    };
}
